      // 设置flash路径,用于在videojs发现浏览器不支持HTML5播放器的时候自动唤起flash播放器
      Video.options.flash.swf = './video-js.swf';
      // 初始化视频，设为全局变量
      this.myPlayer = Video('rtmpVideo', {
        autoplay: true,
        controls: true,//控制条

        muted: true,// 静音
        preload: "auto",// 预加载
        language: "zh-CN",// 初始化语言
        playbackRates: [1, 2, 3, 4, 5, 8, 10, 20],// 播放速度
        techOrder: ['flash'],
    
        sources: [{
