          //     console.log("开始播放");
          // });
          // this.myPlayer.one("error", function (error) {      // 监听错误
          //     console.error("监听到异常，错误信息：%o",error);
          // });
      });
    },
    destroyed() {
      this.myPlayer.dispose()
    },
